	module.exports = {"data":{"post":{"html":"<p>For the first time we've had trick-or-treaters come to the house in Lüllau, 2 groups of girls. I think they were confusing Halloween with <a href=\"http://www.papascott.de/archives/2000/06/12/pentecost/\">Pentecost</a>, since both groups <em>sang</em> at the door. Of course we didn't have any candy, but they were satisfied with coins. Judging from the decorations at the shops and restaurants, it looks like the holiday is catching on more and more in Germany. <em>Süßes oder Saueres!</em></p>","fields":{"title":"Sweet or Sour!","date":"31 Oct 2002","url":"https://papascott.de/2002-10-31-sweet-or-sour/"}}},"pathContext":{"slug":"/2002-10-31-sweet-or-sour/","prev":{"fields":{"title":"Gentoo Linux","date":"30 Oct 2002","slug":"/2002-10-30-gentoo-linux/"}},"next":{"fields":{"title":"Who's On First?","date":"31 Oct 2002","slug":"/2002-10-31-whos-on-first/"}}}}

